@import "https://fonts.googleapis.com/css?family=Manrope";

:root{
    --Light-Cyan: #cee3e9;
    --Neon-Green: #52ffa8;
    --Grayish-Blue: #4e5d73;
    --Dark-Grayish-Blue: #323a49;
    --Dark-Blue: #1f2632;
    --font-weight800:800;
    --font-size:28px;
}

*{
    font-family: 'Manrope';
}

h1,
h2,
h3,
h4,
h5,
h6,
body,
html,
main,
header,
footer,
section,
p,
span,
b,
u,
i{
    margin:0;
    padding:0;
}

body{
    background-color: var(--Dark-Blue);
    
}
main{
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    width: 100%;
    height:100%;
}