.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:2.5rem 2.5rem 4.3rem 2.5rem;
    min-height: 9rem;
    width: 25%;
    gap:1.4rem;
    background-color: var(--Dark-Grayish-Blue);
    border-radius: 1rem;
    background-image: url('./pattern-divider-desktop.svg');
    background-size: 85% auto;
    background-repeat: no-repeat;
    background-position-x:center;
    background-position-y:80% ;
    position: relative;
}

.heading{
    color:var(--Neon-Green);
    letter-spacing: 5px;
}

.advice{
    font-weight: var(--font-weight800);
    color: var(--Light-Cyan);
    text-align: center;
}

.circle{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    height:3.5rem;
    width: 3.5rem;
    background-color: var(--Neon-Green);
    bottom: 0;
    position: absolute;
    transform: translate(0,52%);
    transition: 0.5s ease;

}

.circle:hover{
    box-shadow: 0 0 20px 0 var(--Neon-Green);
}

.circle img{
    height:1.3rem;
    width: 1.3rem;
}

@media screen and (max-width:650px) {
    .content{
        padding:2rem;
        width: 70%;
        min-height: 14rem;
        background-image: url('./pattern-divider-mobile.svg');
    }
}